import Vue from 'vue'
import Router from 'vue-router'
import Account from '../components/account'
import Poll from '../components/poll'
import Login from '../components/login'
import store from '../store'
import RegCoach from "../components/view/coach/RegCoach";
import CoachAccount from "../components/view/coach/CoachAccount";
import GroupsList from "../components/view/coach/GroupsList";
import GroupUsersList from "../components/view/coach/GroupUsersList";
import CoachLogin from "../components/view/coach/CoachLogin";
import TrainingsList from "../components/view/coach/TrainingsList";
import VRLogin from "../components/view/vr/VRLogin";
import VRAccount from "../components/view/vr/VRAccount";

Vue.use(Router);

const ifNotAuthenticated = (to, from, next) => {
    console.log(store.getters.isAuthenticated);
    if (!store.getters.isAuthenticated) {
        next();
        return
    }
    next('/')
};

const ifAuthenticated = (to, from, next) => {
    console.log(store.getters.isAuthenticated);
    if (store.getters.isAuthenticated) {
        next();
        return
    }
    next('/login')
};

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/',
            redirect: 'coach_account'
        },
        {
            path: '/account',
            name: 'Account',
            component: Account,
            beforeEnter: ifAuthenticated
        },
        {
            path: '/login',
            name: 'login',
            component: CoachLogin,
            beforeEnter: ifNotAuthenticated
        },
        {
            path: '*',
            redirect: '/'
        },

        {
            path: '/poll',
            name: 'poll',
            component: Poll,
            //beforeEnter: ifAuthenticated
        },

        // COACH
        {
            path: '/coach/login',
            name: 'coach_login',
            component: CoachLogin,
            beforeEnter: ifNotAuthenticated
        },
        {
            path: '/coach/reg',
            name: 'coach_reg',
            component: RegCoach,
            beforeEnter: ifNotAuthenticated
        },
        {
            path: '/coach/account',
            name: 'coach_account',
            component: CoachAccount,
            beforeEnter: ifAuthenticated,
            children: [
                {
                    path: 'groups',
                    name: 'coach_groups_list',
                    component: GroupsList,
                },
                {
                    path: 'groups/:id_group/users',
                    name: 'coach_group_users',
                    component: GroupUsersList,
                },
                {
                    path: 'trainings',
                    name: 'coach_trainings_list',
                    component: TrainingsList,
                }
            ]
        },

        // VR
        {
            path: '/vr/login',
            name: 'vr_login',
            component: VRLogin,
            beforeEnter: ifNotAuthenticated
        },
        {
            path: '/vr/account',
            name: 'vr_account',
            component: VRAccount,
            beforeEnter: ifAuthenticated,
            children: [
            ]
        },
    ]
})



