<template>
    <div class="app_wrap">
        <div class="main_menu">
            <div class="profile">
                <div class="profile_img"></div>
                <div class="profile_name">Иванов Иван Иванович</div>
            </div>
            <div class="nav">
                <router-link :to="{name: 'coach_account'}">
                    <i class="fas fa-home"></i>Главная
                </router-link>
                <router-link :to="{name: 'coach_groups_list'}">
                    <i class="fas fa-users"></i>Группы
                </router-link>
                <router-link :to="{name: 'coach_trainings_list'}">
                    <i class="fas fa-chalkboard-teacher"></i>Тренинги
                </router-link>
                <router-link :to="{name: 'login'}" @click.native="logout">
                    <i class="fas fa-sign-out-alt"></i>Выход
                </router-link>
            </div>
        </div>
        <div class="content">
            <div class="tab_wrap">
                <div class="tab_header">Аккаунт тренера</div>
                <div class="tab_content">
                </div>
            </div>
            <transition name="tab">
                <router-view/>
            </transition>
        </div>
    </div>
</template>

<script>
    import apiCall from '@/api/api'
    import store from '@/store/';

    export default {
        name: "CoachAccount",
        data: function () {
            return {
                loading: false,
                ready: false,
                imageData: ''
            };
        },
        computed: {},
        methods: {
            regSave: function () {
                console.log('coach reg');
            },

            userExit: function () {
                window.location.href = 'http://speakerguru.pro/';
            },

            logout: function () {
                store.dispatch('AUTH_LOGOUT')
                    .then(() => {
                        this.$router.push('/login')
                    })
            }
        },

        beforeMount: function () {
            if (!this.isProfileLoaded) {
                store.dispatch('USER_REQUEST').then(() => true);
            }
        }
    }
</script>

<style>
    .app_wrap {
        display: block;
        position: relative;
        overflow: hidden;
        height: 100vh;
        width: 100%;
        font-family: 'Roboto', sans-serif, Arial;
    }

    .main_menu {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        width: 275px;
        z-index: 4;
        /*background: #d1f0f2;*/
        /*background: #e6f7f8;*/
        background: #f4feff;
        overflow: auto;
        box-shadow: 0 0 7px #a7a7a7;
    }

    .main_menu .profile {
        display: block;
        position: relative;
        padding: 10px;
        margin-bottom: 10px;
        font-size: 14px;
        color: #1c4144;
        color: #00aab5;
        text-align: center;
    }

    .main_menu .profile .profile_img {
        display: block;
        height: 150px;
        width: 150px;
        border-radius: 50%;
        margin: 0 auto 10px;
        background: 50% no-repeat #e1e4e6;
        background-size: cover;
        background-image: url(https://pp.userapi.com/c621707/v621707912/29fb3/JcilWBLCflM.jpg);
    }

    .main_menu .profile:after {
        content: "";
        display: block;
        position: absolute;
        bottom: 0;
        left: 10px;
        right: 10px;
        height: 1px;
        background: #00aab5;
    }


    .main_menu .nav a {
        display: block;
        text-align: left;
        position: relative;
        font-size: 14px;
        font-weight: 300;
        line-height: 18px;
        color: #00aab5;
        -webkit-transition: .3s ease;
        transition: .3s ease;
        text-decoration: none;
        opacity: .7;
        padding: 8px 5px 8px 40px;
    }

    .main_menu .nav .navs_cont a {
        padding: 8px 10px 8px 20px;
    }

    .main_menu .nav a:hover,
    .main_menu .nav a:visited,
    .main_menu .nav a.active {
        background: #d1f0f2;
        opacity: 1;
    }

    .main_menu .nav a:after {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        background: #00aab5;
        width: 2px;
        opacity: 0;
        -webkit-transition: opacity .3s ease;
        transition: opacity .3s ease;
    }

    .main_menu .nav a.router-link-exact-active {
        background: #d1f0f2;
        opacity: 1;
    }

    .main_menu .nav a:hover:after,
    .main_menu .nav a.router-link-exact-active:after {
        opacity: 1;
    }

    .main_menu .nav a > i {
        display: block;
        position: absolute;
        top: 50%;
        margin-top: -15px;
        left: 5px;
        height: 30px;
        width: 30px;
        text-align: center;
        line-height: 30px !important;
        font-size: 14px;
        color: #000;
        opacity: .4;
    }

    .content {
        position: absolute;
        top: 0;
        left: 275px;
        right: 0;
        bottom: 0;
        z-index: 1;
    }

    .content > .tab_wrap {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        overflow: hidden;
        padding: 60px 10px 10px;
        box-shadow: 0 0 7px #a7a7a7;
        z-index: 1;
    }

    .tab_wrap .tab_header {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        height: 50px;
        line-height: 50px;
        text-align: center;
        background: #00aab5;
        color: #fff;
        font-size: 20px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        box-shadow: 0 0 7px #a7a7a7;
        z-index: 3;
    }

    .tab_wrap .tab_content {
        position: absolute;
        top: 50px;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        overflow: auto;
        background: #fff;
        z-index: 1;
    }

    .content h4 {
        font-size: 14px;
        margin-bottom: 20px;
        text-align: center;
    }

    .tab-enter-active, .tab-leave-active {
        margin-top: 0;
        opacity: 1;
        transition: .4s;
    }

    .tab-enter, .tab-leave-to {
        opacity: 0;
        margin-left: 50px;
    }

    .v-icon {
        font-size: 24px !important;
    }
    th .v-icon {
        font-size: 16px !important;
    }
</style>


