<template>
    <div class="app_wrap">
        <div class="reg_header">Регистрация тренера</div>
        <div class="reg_content">
            <loader :show="loading"/>
            <div v-if="!ready">
                <h4>Пожалуйста, заполните форму</h4>
                <div class="answers">
                    <div class="container fill-height justify-center">
                        <v-image-input
                                v-model="values.photo"
                                :image-quality="0.85"
                                clearable
                                image-format="jpeg"
                        />
                    </div>
                    <v-text-field v-model="values.email" label="Email *" :rules="[rules.required]"></v-text-field>
                    <v-text-field v-model="values.display_name" label="Отображаемое имя *"
                                  :rules="[rules.required]"></v-text-field>
                    <v-text-field v-model="values.phone_model" label="Модель телефона *"
                                  :rules="[rules.required]"></v-text-field>
                    <div class="text-xs-right">
                        <v-btn @click.native="regSave" color="#00aab5" :dark="true">Зарегистрироваться</v-btn>
                    </div>
                </div>
            </div>
            <div v-else style="text-align: center">
                <h4>Благодарим Вас за регистрацию!</h4>
                <router-link :to="{name: 'login'}">На страницу входа</router-link>
            </div>
        </div>
    </div>
</template>

<script>
    import apiCall from '@/api/api';
    import Loader from "@/components/elems/loader";

    export default {
        name: "RegCoach",
        components: {Loader},
        data: function () {
            return {
                loading: false,
                ready: true,
                values: {
                    id_type: 4705521,
                    photo: '',
                    email: null,
                    display_name: null,
                    phone_model: null,
                },
                rules: {
                    required: function (value) {
                        return value ? true : 'Это обязательное поле'
                    }
                }
            };
        },
        computed: {},
        methods: {
            regSave: function () {
                this.loading = true;
                apiCall({
                    url: 'coach_reg',
                    data: this.values
                }).then(response => {
                    console.log(response.data);
                    this.ready = true;
                }).catch(error => {
                    //alert(error);
                    console.log(error);
                }).finally(() => (self.loading = false));
            },

            userExit: function () {
                document.location.href = 'http://speakerguru.pro/';
            }
        }
    }
</script>

<style scoped>
    .app_wrap {
        display: block;
        height: 100vh;
        width: 100%;
        font-family: 'Roboto', sans-serif, Arial;
    }

    .reg_header {
        width: 100%;
        height: 50px;
        line-height: 50px;
        text-align: center;
        background: #00aab5;
        color: #fff;
        font-size: 20px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        box-shadow: 0 0 7px #a7a7a7;
    }

    .reg_content {
        padding: 20px;
        max-width: 600px;
        margin: 0 auto;
    }

    .reg_content h4 {
        font-size: 14px;
        margin-bottom: 20px;
        text-align: center;
    }

    .progress_bar {
        width: 100%;
        height: 7px;
        border-radius: 7px;
        background: #dadada;
        margin-bottom: 20px;
        overflow: hidden;
    }

    .progress_bar > div {
        height: 7px;
        min-width: 3px;
        background: #00aab5;
        border-radius: 7px;
        transition: width .7s ease;
    }

    .answers label {
        display: inline-block;
        vertical-align: top;
        width: calc(33.333% - 20px);
        margin: 10px;
        color: #8f8f8f;
        cursor: pointer;
    }

    .answers label:first-child,
    .answers label:last-child {
        margin: 10px 0;
    }

    .answers label > i {
        display: block;
        width: 100%;
        font-size: 40px;
        line-height: 70px;
        text-align: center;
        transition: color .3s ease;
    }

    .answers label:hover > i {
        color: #00aab5;
    }

    .answers label > span {
        display: block;
        text-align: center;
        font-size: 12px;
    }
</style>


