<template>
    <div class="login_container">
        <transition name="fadebg">
            <div class="login_bg_img" v-show="show_bg_img" :style="{ 'background-image': 'url(' + bg_img + ')' }"></div>
        </transition>
        <div class="login_tab" v-show="show_tab">
            <div class="helper"></div>
            <transition name="fadelogo">
                <div class="login_wrap" v-show="show_logo_img" :class="show_form ? 'active' : ''">
                    <div class="login_logo" :style="{ 'background-image': 'url(' + logo_img + ')' }"></div>
                    <transition name="fadetab">
                        <!--<div class="login_content" v-show="show_form" :class="show_form ? 'active' : ''">-->
                        <div class="login_content" :class="show_form ? 'active' : ''">
                            <v-form class="login_form" @submit.prevent="submitForm" v-model="valid" ref="form">
                                <h2>Вход в аккаунт</h2>
                                <v-text-field
                                        ref="login"
                                        color="primary"
                                        v-model="username"
                                        label="Логин *"
                                        name="username"
                                        :error="error.status"
                                        :error-messages="error.messages"
                                        :rules="[rules.required]"
                                        prepend-icon="person"
                                        :class="{ 'activate-label': dirtyLogin }"
                                        dark
                                ></v-text-field>
                                <v-text-field
                                        v-model="password"
                                        :append-icon="show_pass ? 'visibility_off' : 'visibility'"
                                        :type="show_pass ? 'text' : 'password'"
                                        :rules="[rules.required]"
                                        :error="error.status"
                                        prepend-icon="lock"
                                        color="primary"
                                        :dark="true"
                                        name="password"
                                        label="Пароль *"
                                        hint="Минимум 4 символа"
                                        :class="{ 'activate-label': dirtyPwd }"
                                        counter
                                        @click:append="show_pass = !show_pass"
                                ></v-text-field>
                                <v-btn
                                        large
                                        color="primary"
                                        :dark="true"
                                        type="submit"
                                        :loading="loading"
                                        :disabled="loading"
                                        @click="loader = 'loading'"
                                >Войти
                                </v-btn>
                            </v-form>
                        </div>
                    </transition>
                </div>
            </transition>
        </div>
    </div>
</template>

<script>
    import {AUTH_REQUEST} from '@/store/actions/auth';

    export default {
        name: 'CoachLogin',
        data: function () {
            return {
                loading: false,
                show_tab: false,
                show_bg_img: false,
                show_logo_img: false,
                show_form: false,
                show_pass: false,
                bg_img: '/static/img/login/login_bg.jpg',
                //bg_img: 'https://pp.userapi.com/bC6yPv_iL_STb4rEC2MeUhKI-YeVSQsbds8q4g/jYmBGUeqJgs.jpg',
                //logo_img: '/static/img/login/logo_vert.png',
                //logo_img: 'https://mywishcard.com/s/i2/37/8/470x0_48fNtY1bcUp7y4ytfNRovAuSfnZLYKmT___jpg____4_da9f401e.jpg',
                logo_img: '/static/img/login/speakerguru_logo.png',
                valid: false,
                error: {
                    status: false,
                    timer: null,
                    messages: []
                },
                username: null,
                password: null,
                dirtyLogin: null,
                dirtyPwd: null,
                rules: {
                    required: function (value) {
                        return value ? true : 'Это обязательное поле'
                    }
                }
            }
        },

        methods: {
            load_bg_img: function () {
                let img = document.createElement('img');
                const self = this;

                img.onload = function () {
                    self.show_bg_img = true
                };

                img.src = this.bg_img
            },
            load_logo_img: function () {
                let img = document.createElement('img');
                const self = this;

                img.onload = img.onerror = function () {
                    self.show_logo_img = true
                };

                setTimeout(function () {
                    self.show_form = true;
                }, 1000);

                img.src = this.logo_img
            },

            submitForm: function () {
                if (!this.$refs.form.validate()) {
                    return false;
                }
                this.loading = true;

                //const {username, password} = this;
                const self = this;
                let formData = new FormData();
                formData.append('username', this.username);
                formData.append('password', this.password);
                //formData.append('company', "port");
                this.$store.dispatch(AUTH_REQUEST, formData).then(() => {
                    this.$router.push({name: 'coach_account'});
                    self.loading = false
                }).catch((err) => {
                    console.log(err.response);
                    self.loading = false;
                    if (err.response.status === 401) {
                        self.error.status = true;
                        self.error.messages.push("Неправильный логин или пароль");
                        if (self.error.timer) {
                            clearTimeout(self.error.timer);
                        }
                        self.error.timer = setTimeout(() => {
                            self.clearError();
                        }, 3000);
                    }
                    if (err.response.status === 400) {
                        self.error.status = true;
                        self.error.messages.push(err.response.data.error_description);
                        if (self.error.timer) {
                            clearTimeout(self.error.timer);
                        }
                        self.error.timer = setTimeout(() => {
                            self.clearError();
                        }, 3000);
                    }
                })
            },

            clearError: function () {
                if (this.error.timer) {
                    clearTimeout(this.error.timer);
                }
                this.error.status = false;
                this.error.messages = [];
                this.error.timer = null;
            }
        },

        created: function () {
        },

        mounted: function () {
            this.load_bg_img();
            this.load_logo_img();
            this.show_tab = true;

            // fix chrome autocomplete
            let times = 0;
            const interval = setInterval(() => {
                times += 1;
                if ((this.dirtyLogin && this.dirtyPwd) || times === 100) {
                    clearInterval(interval);
                }
                this.dirtyLogin = document.querySelector('input[name="username"]:-webkit-autofill');
                this.dirtyPwd = document.querySelector('input[name="password"]:-webkit-autofill');
            }, 200);
        }

    }
</script>

<style>

    .login_container {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        overflow: hidden;
        /*background: #323639;*/
        background: #007a82;
    }

    .login_bg_img {
        position: absolute;
        z-index: 1;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: no-repeat center;
        background-size: cover;
        opacity: 1;
    }

    .login_tab {
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        width: 100%;
        background: transparent;
        overflow: hidden;
        font-size: 0;
        opacity: 1;
        z-index: 2;
    }

    .helper {
        display: inline-block;
        vertical-align: middle;
        font-size: 0;
        padding: 0;
        margin: 0;
        height: 100%;
        width: 0;
    }

    .login_wrap {
        display: inline-block;
        position: relative;
        vertical-align: middle;
        overflow: hidden;
        /*min-height: 300px; */
        padding-left: 50%;
        width: 100%;
        box-shadow: 0 0 10px #000;
        background: #fff;
    }

    .login_wrap .login_logo {
        position: absolute;
        z-index: 1;
        top: 50px;
        bottom: 50px;
        width: 310px;
        right: 50%;
        margin-right: -155px;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: 50% 50%;
        opacity: 1;
        -webkit-transition: .5s ease;
        transition: .5s ease;
    }

    .login_wrap.active .login_logo {
        margin-right: 25px;
    }

    .login_content {
        /*position: absolute;*/
        position: relative;
        z-index: 1;
        top: 0;
        bottom: 0;
        /*width: 50%;*/
        width: 100%;
        right: -100px;
        padding-left: 24px;
        text-align: left;
        opacity: 0;
        background: #007a82;
        transition: .5s ease;
    }

    .login_content.active {
        opacity: 1;
        right: 0;
    }

    .login_form {
        display: inline-block;
        min-width: 320px;
        max-width: 400px;
        text-align: center;
        padding: 35px 0 25px;
    }

    .login_content h2 {
        display: block;
        font-size: 24px;
        padding: 0;
        margin: 0 0 20px 0;
        color: #fff;
        font-weight: normal;
        text-align: center;
    }

    .fadebg-enter-active, .fadebg-leave-active {
        transition: opacity 2s;
    }

    .fadebg-enter, .fadetab-leave-to {
        opacity: 0;
    }

    .fadelogo-enter-active, .fadelogo-leave-active {
        transition: opacity .5s;
    }

    .fadelogo-enter, .fadelogo-leave-to {
        opacity: 0;
    }

    .fadetab-enter-active, .fadetab-leave-active {
        transition: .5s;
    }

    .fadetab-enter, .fadetab-leave-to {
        right: -100px;
        opacity: 0;
    }

    .v-input {
        height: 64px;
    }

    .activate-label .v-text-field__slot label.v-label {
        max-width: 133%;
        -webkit-transform: translateY(-18px) scale(0.75);
        transform: translateY(-18px) scale(0.75) !important;
    }

    .login_container .v-text-field input:-webkit-autofill {
        -webkit-box-shadow: inset 0 0 0 50px #007a82 !important;
        -webkit-text-fill-color: #fff !important;
        color: #fff !important;
        caret-color: #fff !important;
        background-color: #007a82 !important;
        outline: none;
    }

    @media (max-width: 768px) {

        .login_wrap {
            padding: 0;
        }

        .login_content {
            width: 100%;
            padding: 0;
            text-align: center;
        }
    }

</style>



