<template>
    <div class="app_wrap">
        <div class="content">
            <div class="tab_wrap">
                <div class="tab_header">
                    {{headTitle}}
                    <i class="fa fa-sign-out-alt logout_icon" @click.prevent="logout"></i>
                </div>
                <div class="tab_content" v-if="isCoach">
                    <v-data-table
                            :headers="headers"
                            :items="currGroups"
                            :loading="loading"
                            class="elevation-1"
                            style="margin-top: 15px"
                    >
                        <template v-slot:items="props">
                            <td>{{ props.item.id_version }}</td>
                            <td>{{ props.item.attr_4705556 }}</td>
                            <td>{{ props.item.count }}</td>
                            <td class="justify-center">
                                <v-icon
                                        small
                                        class="mr-2"
                                        @click="listItem(props.item)"
                                >list
                                </v-icon>
                                <v-icon
                                        small
                                        class="mr-2"
                                        @click="editItem(props.item)"
                                >edit
                                </v-icon>
                                <v-icon
                                        small
                                        @click="deleteItem(props.item)"
                                >delete
                                </v-icon>
                            </td>
                        </template>
                        <template v-slot:no-data>
                            Нет данных
                        </template>
                    </v-data-table>
                </div>
                <div class="tab_content" v-else>
                    <div class="container fill-height justify-center">
                        <v-btn color="primary" @click="vrLink('speaker')">Режим спикер</v-btn>
                        <v-btn color="primary" @click="vrLink('listener')">Режим слушатель</v-btn>
                    </div>
                </div>
            </div>
            <transition name="tab">
                <router-view/>
            </transition>
        </div>
    </div>
</template>

<script>
    import apiCall from '@/api/api';
    import {mapGetters, mapState} from 'vuex';
    import {AUTH_LOGOUT} from '@/store/actions/auth';
    import {USER_REQUEST} from '@/store/actions/user';

    export default {
        name: "VRAccount",
        data: function () {
            return {
                loading: false,
                ready: false,
                headers: [
                    {text: 'ID', value: 'id_version'},
                    {text: 'Название', value: 'attr_4705556'},
                    {text: 'Кол-во участников', value: 'count'},
                    {text: '#', value: 'name', sortable: false}
                ],

            };
        },
        computed: {
            ...mapGetters(['getProfile', 'isAuthenticated', 'isProfileLoaded']),
            headTitle: function () {
                if (!this.isProfileLoaded) {
                    return 'Аккаунт';
                }
                if (this.isCoach) {
                    return 'Аккаунт тренера'
                }
                return 'Аккаунт участника';
            },

            isCoach: function () {
                return this.isProfileLoaded && this.getProfile.id_role === 4705567;
            },

            currGroups: function () {
                return [];
            }

        },
        methods: {
            regSave: function () {
                console.log('coach reg');
            },

            userExit: function () {
                window.location.href = 'http://speakerguru.pro/';
            },

            logout: function () {
                this.$store.dispatch('AUTH_LOGOUT')
                    .then(() => {
                        this.$router.push('/login')
                    })
            },

            vrLink: function (role) {
                let xr = role === 'speaker';
                window.location.href = 'sg://startGame?sessionId=12345&role=' + role + '&login=len&password=1234&locale=ru&avatar=rosy&time=300&region=ru&nickname=abelevtsov&listenersCount=10&xr=' + xr + '&place=Office';
            }
        },

        beforeMount: function () {
            if (!this.isProfileLoaded) {
                this.$store.dispatch(USER_REQUEST).then(() => true);
            }
        }
    }
</script>

<style scoped>
    .app_wrap {
        display: block;
        position: relative;
        overflow: hidden;
        height: 100vh;
        width: 100%;
        font-family: 'Roboto', sans-serif, Arial;
    }

    .content {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 1;
    }

    .content > .tab_wrap {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        overflow: hidden;
        padding: 60px 10px 10px;
        box-shadow: 0 0 7px #a7a7a7;
        z-index: 1;
    }

    .tab_wrap .tab_header {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        height: 50px;
        line-height: 50px;
        text-align: center;
        background: #00aab5;
        color: #fff;
        font-size: 20px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        box-shadow: 0 0 7px #a7a7a7;
        z-index: 3;
    }

    .logout_icon {
        position: absolute;
        top: 50%;
        margin-top: -20px;
        height: 40px;
        width: 40px;
        text-align: center;
        line-height: 40px !important;
        cursor: pointer;
        right: 10px;
    }

    .tab_wrap .tab_content {
        position: absolute;
        top: 50px;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        overflow: auto;
        background: #fff;
        z-index: 1;
    }

    .content h4 {
        font-size: 14px;
        margin-bottom: 20px;
        text-align: center;
    }

    .tab-enter-active, .tab-leave-active {
        margin-top: 0;
        opacity: 1;
        transition: .4s;
    }

    .tab-enter, .tab-leave-to {
        opacity: 0;
        margin-left: 50px;
    }

    th .v-icon {
        font-size: 16px !important;
    }
</style>


