<template>
    <div class="app_wrap">
        <div class="header">Опрос по результатам выступления</div>
        <div class="content">
            <h4>{{currQuestion}}</h4>
            <div class="progress_bar">
                <div :style="{'width': progressWidth + '%'}"></div>
            </div>
            <div class="answers" v-if="curr_index !== list.length">
                <div v-for="(curr, index) in list" v-show="index === curr_index">
                    <label @click.prevent="setAnswer(curr, index, 1)">
                        <i class="far fa-smile"></i>
                        <span>Критерий был продемонстрирован в течение всего выступления на высоком уровне</span>
                    </label>
                    <label @click.prevent="setAnswer(curr, index, 0.5)">
                        <i class="far fa-meh"></i>
                        <span>Критерий продемонстрирован слабо, требуется развитие</span>
                    </label>
                    <label @click.prevent="setAnswer(curr, index, 0)">
                        <i class="far fa-frown"></i>
                        <span>Критерий не продемонстрирован или проявился негативно</span>
                    </label>
                </div>
            </div>
            <div v-else>
                <p>Благодарим Вас за ответы!</p>
                <v-btn @click.native="userExit">Выход</v-btn>
            </div>
        </div>
    </div>
</template>

<script>
    import apiCall from '@/api/api'

    export default {
        name: "poll",
        data: function () {
            return {
                curr_index: 0,
                list: [
                    {title: 'Привлечение внимания'},
                    {title: 'Регламент'},
                    {title: 'Аргументация по модели ТАП'},
                    {title: 'Паравербалика (богатство интонаций, пауз и т.д.)'},
                    {title: 'Подведение итогов'},
                    {title: 'Призыв к действию и предложение следующего шага'},
                    {title: 'Ответы на вопросы'}
                ],
                answers: []
            };
        },
        computed: {
            currQuestion: function () {
                if (this.curr_index === this.list.length) {
                    return 'Опрос завершен';
                }
                return 'Вопрос ' + (this.curr_index + 1) + ': ' + (this.list[this.curr_index] && this.list[this.curr_index].title || 'error');
            },

            progressWidth: function () {
                //return Math.ceil(((this.curr_index + 1) / this.list.length) * 100);
                return Math.ceil((this.curr_index / this.list.length) * 100);
            }
        },
        methods: {
            setAnswer: function (curr, index, mark) {
                this.curr_index = index + 1;
                this.answers[index] = {
                    title: curr.title,
                    mark: mark
                };

                if (this.curr_index === this.list.length) {
                    // set request here
                    this.saveResults();
                }
            },

            saveResults: function () {
                apiCall({
                    url: 'api_post',
                    data: this._getValues(),
                    url_ext: null
                }).then(response => {
                    console.log(response);
                }).catch((err) => {
                    console.log(err.response);
                }).finally(() => {
                });
            },

            _getValues: function () {
                return {
                    user_id: this.$route.query.user,
                    lesson_id: this.$route.query.lesson,
                    values: this.values
                };
            },

            userExit: function () {
                document.location.href = 'http://speakerguru.pro/';
            }
        }
    }
</script>

<style scoped>
    .app_wrap {
        display: block;
        height: 100vh;
        width: 100%;
        font-family: 'Roboto', sans-serif, Arial;
    }

    .header {
        width: 100%;
        height: 50px;
        line-height: 50px;
        text-align: center;
        background: #00aab5;
        color: #fff;
        font-size: 20px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        box-shadow: 0 0 7px #a7a7a7;
    }

    .content {
        padding: 20px;
        max-width: 600px;
        margin: 0 auto;
    }

    .content h4 {
        font-size: 14px;
        margin-bottom: 20px;
    }

    .progress_bar {
        width: 100%;
        height: 7px;
        border-radius: 7px;
        background: #dadada;
        margin-bottom: 20px;
        overflow: hidden;
    }

    .progress_bar > div {
        height: 7px;
        min-width: 3px;
        background: #00aab5;
        border-radius: 7px;
        transition: width .7s ease;
    }

    .answers label {
        display: inline-block;
        vertical-align: top;
        width: calc(33.333% - 20px);
        margin: 10px;
        color: #8f8f8f;
        cursor: pointer;
    }

    .answers label:first-child,
    .answers label:last-child {
        margin: 10px 0;
    }

    .answers label > i {
        display: block;
        width: 100%;
        font-size: 40px;
        line-height: 70px;
        text-align: center;
        transition: color .3s ease;
    }

    .answers label:hover > i {
        color: #00aab5;
    }

    .answers label > span {
        display: block;
        text-align: center;
        font-size: 12px;
    }
</style>


