<template>
    <div class="tab_wrap">
        <div class="tab_header">Список участников группы</div>
        <div class="tab_content">
            <v-toolbar flat color="white">
                <v-toolbar-title>{{groupName}}</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-dialog v-model="dialog" max-width="500px">
                    <template v-slot:activator="{ on }">
                        <v-btn color="primary" dark class="mb-2" v-on="on">Новый участник</v-btn>
                    </template>
                    <v-card>
                        <v-card-title>
                            <span class="headline">{{ formTitle }}</span>
                        </v-card-title>

                        <v-card-text>
                            <v-container grid-list-md>
                                <v-layout wrap>
                                    <v-flex xs12>
                                        <v-text-field v-model="editedItem.attr_4705527" label="Email"></v-text-field>
                                    </v-flex>
                                </v-layout>
                            </v-container>
                        </v-card-text>

                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="blue darken-1" flat @click="close">Отмена</v-btn>
                            <v-btn color="blue darken-1" flat @click="save">Сохранить</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-toolbar>

            <v-text-field
                    v-model="search"
                    append-icon="search"
                    label="Поиск"
                    single-line
                    hide-details
            ></v-text-field>

            <v-data-table
                    :headers="headers"
                    :items="currValues"
                    :search="search"
                    class="elevation-1"
                    style="margin-top: 15px"
            >
                <template v-slot:items="props">
                    <td>{{ props.item.id_version }}</td>
                    <td>{{ props.item.attr_4705527 }}</td>
                    <td class="justify-center">
                        <!--<v-icon
                                small
                                class="mr-2"
                                @click="editItem(props.item)"
                        >edit
                        </v-icon>-->
                        <v-icon
                                small
                                @click="deleteItem(props.item)"
                        >delete
                        </v-icon>
                    </td>
                </template>
                <template v-slot:no-data>
                    <v-btn color="primary" @click="initialize">Обновить</v-btn>
                </template>
            </v-data-table>
        </div>
    </div>
</template>

<script>
    import apiCall from '@/api/api'

    export default {
        name: "CoachGroupsList",
        data: () => ({
            loading: false,
            ready: false,
            search: '',
            dialog: false,
            headers: [
                {text: 'ID', value: 'id_version'},
                {text: 'Email', value: 'attr_4705527'},
                {text: '#', value: 'name', sortable: false}
            ],
            rows: [],
            editedIndex: -1,
            editedItem: {
                id_type: 4705514,
                attr_4705527: '',
            },
            defaultItem: {
                id_type: 4705514,
                attr_4705527: '',
            }
        }),

        computed: {
            formTitle() {
                return this.editedIndex === -1 ? 'Новый участник' : 'Редактирование участника'
            },

            groupName() {
                return this.$route.params.name || '';
            },

            currValues(){
                return typeof this.rows === 'object' && this.rows || [];
            }
        },

        watch: {
            dialog(val) {
                val || this.close()
            }
        },

        created() {
            this.initialize()
        },

        methods: {
            initialize() {
                const self = this;
                this.loading = true;

                apiCall({
                    url: 'coach_group_users_list',
                    url_ext: this.$route.params.id_group + '/users'
                }).then(response => {
                    this.rows = response.data;
                    this.close()
                }).catch(error => {
                    alert(error);
                    console.log(error);
                }).finally(() => (self.loading = false));

                /*this.rows = [
                    {
                        id_version: 1,
                        attr_4705527: 'test@mail.com'
                    },
                    {
                        id_version: 2,
                        attr_4705527: 'test@mail.com'
                    },
                    {
                        id_version: 3,
                        attr_4705527: 'test@mail.com'
                    },
                    {
                        id_version: 4,
                        attr_4705527: 'test@mail.com'
                    },
                ]*/
            },

            editItem(item) {
                this.editedIndex = this.rows.indexOf(item)
                this.editedItem = Object.assign({}, item)
                this.dialog = true
            },

            deleteItem(item) {
                const index = this.rows.indexOf(item)
                confirm('Вы уверены, что хотите удалить этого участника?') && this.rows.splice(index, 1)
            },

            close() {
                this.dialog = false
                setTimeout(() => {
                    this.editedItem = Object.assign({}, this.defaultItem)
                    this.editedIndex = -1
                }, 300)
            },

            save() {
                const self = this;
                this.loading = true;

                apiCall({
                    url: 'coach_group_user_save',
                    data: this.editedItem,
                    url_ext: this.$route.params.id_group + '/users'
                })
                    .then(response => {
                        if (this.editedIndex > -1) {
                            Object.assign(this.rows[this.editedIndex], this.editedItem)
                        } else {
                            this.rows.push(this.editedItem)
                        }
                        this.close()
                    })
                    .catch(error => {
                        alert(error);
                        console.log(error);
                    })
                    .finally(() => (self.loading = false));
            }
        }
    }
</script>

<style>
    .tab_content {
        padding: 10px;
        font-size: 12px;
    }

    table.table {
        margin: 0 auto;
        width: 98%;
        max-width: 98%;
    }

    td .v-icon {
        font-size: 16px !important;
    }

    .datatable-cell-wrapper {
        width: inherit;
        position: relative;
        z-index: 4;
        padding: 10px 24px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    .datatable__expand-content .card__text {
        z-index: 3;
        position: relative;
    }

    .datatable-container {
        position: absolute;
        background-color: white;
        top: -50px;
        left: -14px;
        right: -14px;
        bottom: 0;
        z-index: 2;
        border: 1px solid #ccc;
        box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.15), 0 1px 10px 0 rgba(0, 0, 0, 0.15), 0 2px 4px -1px rgba(0, 0, 0, 0.2);
    }

</style>


