/* eslint-disable promise/param-names */
import {AUTH_REQUEST, AUTH_ERROR, AUTH_SUCCESS, AUTH_LOGOUT} from '../actions/auth';
import {USER_REQUEST} from '../actions/user';
import apiCall from '../../api/api';
import settings from '../../settings/settings';
import axios from 'axios'

const state = {
    access_token: localStorage.getItem(settings.token_key) || '',
    refresh_token: '',
    expires_in: 0,
    token_type: '',
    status: '',
    hasLoadedOnce: false
};

//console.log(localStorage.getItem(settings.token_key));

const getters = {
    isAuthenticated: function (state) {
        return !!state.access_token;
    },
    authStatus: function (state) {
        return state.status;
    },
    accessToken: function (state) {
        return state.access_token;
    },
    refreshToken: function (state) {
        return state.refresh_token;
    }
};

const actions = {
    [AUTH_REQUEST]: ({commit, dispatch}, user) => {
        return new Promise((resolve, reject) => {
            commit(AUTH_REQUEST);
            apiCall({url: 'auth', data: user})
                .then(resp => {
                    //console.log(resp);
                    commit(AUTH_SUCCESS, resp);
                    dispatch(USER_REQUEST);
                    resolve(resp)
                })
                .catch(err => {
                    commit(AUTH_ERROR, err);
                    reject(err)
                })
        })
    },
    [AUTH_LOGOUT]: ({commit, dispatch}) => {
        return new Promise((resolve, reject) => {
            commit(AUTH_LOGOUT);
            apiCall({url: 'logout'})
                .then(resp => {
                    //console.log(resp);
                    commit(AUTH_SUCCESS, resp);
                    dispatch(USER_REQUEST);
                    resolve(resp)
                })
                .catch(err => {
                    //commit(AUTH_ERROR, err);
                    reject(err)
                });
            resolve()
        })
    }
};

const mutations = {
    [AUTH_REQUEST]: (state) => {
        state.status = 'loading'
    },
    [AUTH_SUCCESS]: (state, resp) => {
        state.status = 'success';
        state.access_token = resp.data.access_token;
        state.refresh_token = resp.data.refresh_token;
        state.token_type = resp.data.token_type;
        state.expires_in = resp.data.expires_in;
        state.hasLoadedOnce = true;

        localStorage.setItem(settings.token_key, state.access_token);

        // Here set the header of your ajax library to the token value.
        // example with axios

        axios.defaults.headers.common['Authorization'] = 'Bearer ' + resp.data.access_token;
    },
    [AUTH_ERROR]: (state) => {
        state.status = 'error';
        state.hasLoadedOnce = true;
        localStorage.removeItem(settings.token_key);
    },
    [AUTH_LOGOUT]: (state) => {
        state.access_token = '';
        state.refresh_token = '';
        state.token_type = '';
        state.expires_in = 0;
        localStorage.removeItem(settings.token_key);
    }
};

export default {
    state,
    getters,
    actions,
    mutations
}



