<template>
    <div class="tab_wrap">
        <div class="tab_header">Список групп</div>
        <div class="tab_content">
            <loader :show="loading"/>
            <v-toolbar flat color="white">
                <v-toolbar-title></v-toolbar-title>
                <v-spacer></v-spacer>
                <v-dialog v-model="dialog" max-width="500px">
                    <template v-slot:activator="{ on }">
                        <v-btn color="primary" dark class="mb-2" v-on="on">Новая группа</v-btn>
                    </template>
                    <v-card>
                        <v-card-title>
                            <span class="headline">{{ formTitle }}</span>
                        </v-card-title>

                        <v-card-text>
                            <v-container grid-list-md>
                                <v-layout wrap>
                                    <v-flex xs12>
                                        <v-text-field v-model="editedItem.title" label="Название"></v-text-field>
                                    </v-flex>
                                    <v-flex xs6>
                                        <v-menu
                                                v-model="datepicker_1"
                                                :close-on-content-click="false"
                                                :nudge-right="40"
                                                transition="scale-transition"
                                                offset-y
                                                full-width
                                                min-width="290px"
                                        >
                                            <template v-slot:activator="{ on }">
                                                <v-text-field
                                                        v-model="editedItem.date_1"
                                                        label="Дата 1го занятия"
                                                        prepend-icon="event"
                                                        readonly
                                                        v-on="on"
                                                ></v-text-field>
                                            </template>
                                            <v-date-picker no-title scrollable v-model="editedItem.date_1"
                                                           @input="datepicker_1 = false"></v-date-picker>
                                        </v-menu>
                                    </v-flex>
                                    <v-flex xs6>
                                        <v-menu
                                                ref="timepicker_1"
                                                v-model="timepicker_1"
                                                :close-on-content-click="false"
                                                :nudge-right="40"
                                                transition="scale-transition"
                                                offset-y
                                                full-width
                                                min-width="290px"
                                        >
                                            <template v-slot:activator="{ on }">
                                                <v-text-field
                                                        v-model="editedItem.time_1"
                                                        label="Время 1го занятия"
                                                        prepend-icon="access_time"
                                                        readonly
                                                        v-on="on"
                                                ></v-text-field>
                                            </template>
                                            <v-time-picker
                                                    v-if="timepicker_1"
                                                    v-model="editedItem.time_1"
                                                    format="24hr"
                                                    full-width
                                                    @click:minute="$refs.timepicker_1.save(editedItem.time_1)"
                                            ></v-time-picker>
                                        </v-menu>
                                    </v-flex>
                                    <v-flex xs6>
                                        <v-menu
                                                v-model="datepicker_2"
                                                :close-on-content-click="false"
                                                :nudge-right="40"
                                                transition="scale-transition"
                                                offset-y
                                                full-width
                                                min-width="290px"
                                        >
                                            <template v-slot:activator="{ on }">
                                                <v-text-field
                                                        v-model="editedItem.date_2"
                                                        label="Дата 2го занятия"
                                                        prepend-icon="event"
                                                        readonly
                                                        v-on="on"
                                                ></v-text-field>
                                            </template>
                                            <v-date-picker no-title scrollable v-model="editedItem.date_2"
                                                           @input="datepicker_2 = false"></v-date-picker>
                                        </v-menu>
                                    </v-flex>
                                    <v-flex xs6>
                                        <v-menu
                                                ref="timepicker_2"
                                                v-model="timepicker_2"
                                                :close-on-content-click="false"
                                                :nudge-right="40"
                                                transition="scale-transition"
                                                offset-y
                                                full-width
                                                min-width="290px"
                                        >
                                            <template v-slot:activator="{ on }">
                                                <v-text-field
                                                        v-model="editedItem.time_2"
                                                        label="Время 2го занятия"
                                                        prepend-icon="access_time"
                                                        readonly
                                                        v-on="on"
                                                ></v-text-field>
                                            </template>
                                            <v-time-picker
                                                    v-if="timepicker_2"
                                                    v-model="editedItem.time_2"
                                                    format="24hr"
                                                    full-width
                                                    @click:minute="$refs.timepicker_2.save(editedItem.time_2)"
                                            ></v-time-picker>
                                        </v-menu>
                                    </v-flex>
                                    <v-flex xs6>
                                        <v-menu
                                                v-model="datepicker_3"
                                                :close-on-content-click="false"
                                                :nudge-right="40"
                                                transition="scale-transition"
                                                offset-y
                                                full-width
                                                min-width="290px"
                                        >
                                            <template v-slot:activator="{ on }">
                                                <v-text-field
                                                        v-model="editedItem.date_3"
                                                        label="Дата 3го занятия"
                                                        prepend-icon="event"
                                                        readonly
                                                        v-on="on"
                                                ></v-text-field>
                                            </template>
                                            <v-date-picker no-title scrollable v-model="editedItem.date_3"
                                                           @input="datepicker_3 = false"></v-date-picker>
                                        </v-menu>
                                    </v-flex>
                                    <v-flex xs6>
                                        <v-menu
                                                ref="timepicker_3"
                                                v-model="timepicker_3"
                                                :close-on-content-click="false"
                                                :nudge-right="40"
                                                transition="scale-transition"
                                                offset-y
                                                full-width
                                                min-width="290px"
                                        >
                                            <template v-slot:activator="{ on }">
                                                <v-text-field
                                                        v-model="editedItem.time_3"
                                                        label="Время 3го занятия"
                                                        prepend-icon="access_time"
                                                        readonly
                                                        v-on="on"
                                                ></v-text-field>
                                            </template>
                                            <v-time-picker
                                                    v-if="timepicker_3"
                                                    v-model="editedItem.time_3"
                                                    format="24hr"
                                                    full-width
                                                    @click:minute="$refs.timepicker_3.save(editedItem.time_3)"
                                            ></v-time-picker>
                                        </v-menu>
                                    </v-flex>
                                </v-layout>
                            </v-container>
                        </v-card-text>

                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="blue darken-1" flat @click="close">Отмена</v-btn>
                            <v-btn color="blue darken-1" flat @click="save">Сохранить</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-toolbar>

            <v-text-field
                    v-model="search"
                    append-icon="search"
                    label="Поиск"
                    single-line
                    hide-details
            ></v-text-field>

            <v-data-table
                    :headers="headers"
                    :items="currValues"
                    :loading="loading"
                    :search="search"
                    class="elevation-1"
                    style="margin-top: 15px"
            >
                <template v-slot:items="props">
                    <td>{{ props.item.id_version }}</td>
                    <td>{{ props.item.title }}</td>
                    <td>{{ props.item.count_participants }}</td>
                    <td>{{ props.item.time_1 }} {{ props.item.date_1 }}</td>
                    <td>{{ props.item.time_2 }} {{ props.item.date_2 }}</td>
                    <td>{{ props.item.time_3 }} {{ props.item.date_3 }}</td>
                    <td class="justify-center">
                        <v-menu bottom left>
                            <template v-slot:activator="{ on }">
                                <v-btn icon v-on="on">
                                    <v-icon>more_vert</v-icon>
                                </v-btn>
                            </template>

                            <v-list>
                                <v-list-tile @click="listItem(props.item)">
                                    <v-list-tile-avatar>
                                        <v-icon small>group</v-icon>
                                    </v-list-tile-avatar>
                                    <v-list-tile-title>Участники</v-list-tile-title>
                                </v-list-tile>
                                <v-list-tile @click="listItem(props.item)">
                                    <v-list-tile-avatar>
                                        <v-icon small>mic</v-icon>
                                    </v-list-tile-avatar>
                                    <v-list-tile-title>Тренинги</v-list-tile-title>
                                </v-list-tile>
                                <v-list-tile @click="editItem(props.item)">
                                    <v-list-tile-avatar>
                                        <v-icon small>edit</v-icon>
                                    </v-list-tile-avatar>
                                    <v-list-tile-title>Редактировать</v-list-tile-title>
                                </v-list-tile>
                                <v-list-tile @click="deleteItem(props.item)">
                                    <v-list-tile-avatar>
                                        <v-icon small>delete</v-icon>
                                    </v-list-tile-avatar>
                                    <v-list-tile-title>Удалить</v-list-tile-title>
                                </v-list-tile>
                            </v-list>
                        </v-menu>
                    </td>
                </template>
                <template v-slot:no-data>
                    <v-btn color="primary" @click="initialize">Обновить</v-btn>
                </template>
            </v-data-table>
        </div>
    </div>
</template>

<script>
    import apiCall from '@/api/api/';
    import Loader from "@/components/elems/loader/";
    import store from '@/store/';

    export default {
        name: "CoachGroupsList",
        components: {Loader},
        data: () => ({
            loading: false,
            ready: false,
            search: '',
            dialog: false,
            datepicker_1: false,
            datepicker_2: false,
            datepicker_3: false,
            timepicker_1: false,
            timepicker_2: false,
            timepicker_3: false,
            headers: [
                {text: 'ID', value: 'id_version'},
                {text: 'Название', value: 'title'},
                {text: 'Кол-во участников', value: 'count_participants'},
                {text: '1-е занятие', value: 'date_1'},
                {text: '2-е занятие', value: 'date_2'},
                {text: '3-е занятие', value: 'date_3'},
                {text: '#', value: 'name', sortable: false}
            ],
            rows: [],
            editedIndex: -1,
            editedItem: {
                id_type: 4705513,
                title: '',
                date_1: '',
                date_2: '',
                date_3: '',
                time_1: '',
                time_2: '',
                time_3: '',
            },
            defaultItem: {
                id_type: 4705513,
                title: '',
                date_1: '',
                date_2: '',
                date_3: '',
                time_1: '',
                time_2: '',
                time_3: '',
            }
        }),

        computed: {
            formTitle() {
                return this.editedIndex === -1 ? 'Новая группа' : 'Реактирование группы'
            },

            currValues() {
                return typeof this.rows === 'object' && this.rows || [];
            },
            coachId: function () {
                return store.getters.getProfile.id_version;
            }
        },

        watch: {
            dialog(val) {
                val || this.close()
            }
        },

        created() {
            this.initialize()
        },

        methods: {
            initialize() {
                const self = this;
                this.loading = true;

                apiCall({
                    url: 'coach_groups_list'
                }).then(response => {
                    this.rows = response.data;
                    this.close()
                }).catch(error => {
                    //alert(error);
                    console.log(error);
                }).finally(() => (self.loading = false));
            },

            listItem(item) {
                this.$router.push({
                        name: 'coach_group_users',
                        params: {
                            id_group: item.id_version,
                            name: item.title
                        }
                    }
                );
            },

            editItem(item) {
                this.editedIndex = this.rows.indexOf(item);
                this.editedItem = Object.assign({}, item);
                this.dialog = true
            },

            deleteItem(item) {
                const index = this.rows.indexOf(item);
                confirm('Вы уверены, что хотите удалить эту группу?') && this.rows.splice(index, 1)
            },

            close() {
                this.dialog = false;
                setTimeout(() => {
                    this.editedItem = Object.assign({}, this.defaultItem);
                    this.editedIndex = -1
                }, 300)
            },

            save() {
                const self = this;
                this.loading = true;
                this.editedItem.trainer = this.coachId;

                apiCall({url: 'coach_group_save', data: this.editedItem, url_ext: null})
                    .then(response => {
                        if (this.editedIndex > -1) {
                            //Object.assign(this.rows[this.editedIndex], this.editedItem)
                            Object.assign(this.rows[this.editedIndex], response.data)
                        } else {
                            //this.rows.push(this.editedItem)
                            this.rows.push(response.data);
                        }
                        this.close()
                    })
                    .catch(error => {
                        //alert(error);
                        console.log(error);
                    })
                    .finally(() => (self.loading = false));
            }
        }
    }
</script>

<style>
    .tab_content {
        padding: 10px;
        font-size: 12px;
    }

    table.table {
        margin: 0 auto;
        width: 98%;
        max-width: 98%;
    }

    td .v-icon {
        font-size: 16px !important;
    }

    .datatable-cell-wrapper {
        width: inherit;
        position: relative;
        z-index: 4;
        padding: 10px 24px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    .datatable__expand-content .card__text {
        z-index: 3;
        position: relative;
    }

    .datatable-container {
        position: absolute;
        background-color: white;
        top: -50px;
        left: -14px;
        right: -14px;
        bottom: 0;
        z-index: 2;
        border: 1px solid #ccc;
        box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.15), 0 1px 10px 0 rgba(0, 0, 0, 0.15), 0 2px 4px -1px rgba(0, 0, 0, 0.2);
    }

</style>


