<template>
    <transition name="fade">
        <div class="loader" :class="dark ? 'dark' : ''" v-show="show" :style="{background: bg_color}">
            <div class="helper"></div>
            <i class="fas fa-circle-notch fa-spin"
               :style="loaderStyle"></i>
        </div>
    </transition>
</template>

<script>
    export default {
        name: "loader",
        props: {
            show: {
                type: Boolean,
                default: false
            },
            dark: {
                type: Boolean,
                default: false
            },
            bg_color: {
                type: String,
                default: 'rgba(255, 255, 255, 0.7)'
            },
            size: {
                type: Number,
                default: 28
            }
        },
        computed: {
            loaderStyle: function () {
                return {
                    'height': this.size + 'px',
                    'width': (this.size + 1) + 'px',
                    'text-align': 'center',
                    'line-height': (this.size - 2) + 'px!important',
                    'font-size': this.size + 'px'
                };
            }
        }
    }
</script>

<style scoped>

    .loader {
        display: block;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: rgba(255, 255, 255, 0.7);
        text-align: center;
        transition: .3s ease;
        opacity: 1;
        font-size: 0;
        user-select: none;
        z-index: 999;
    }

    .loader.dark {
        background: #2c3e50;
    }

    .helper {
        display: inline-block;
        width: 0;
        height: 100%;
        padding: 0;
        margin: 0;
        vertical-align: middle;
    }

    .loader > i {
        display: inline-block;
        font-size: 20px;
        color: #8e9aab;
        line-height: 20px !important;
        vertical-align: middle;
    }

    .fade-enter-active, .fade-leave-active {
        transition: opacity .3s;
    }

    .fade-enter, .fade-leave-to {
        opacity: 0;
    }
</style>


