<template>
  <v-app id="inspire">
    <v-content>
      <v-container fluid fill-height>
        <v-layout align-center justify-center>
          <v-flex xs12 sm8 md4>
            <v-card class="elevation-12">
              <v-toolbar dark color="primary">

                <v-toolbar-title>
                  {{message}}
                </v-toolbar-title>
                <v-spacer></v-spacer>
                <v-tooltip bottom>
                  <!--			<v-btn
                                      icon
                                      large
                                      :href="source"
                                      target="_blank"
                                      slot="activator"
                                    >
                                      <v-icon large>assignment_ind</v-icon>
                                    </v-btn>
                                       <span>REGISTER   {{ message }}</span>
                     -->

                </v-tooltip>
              </v-toolbar>
              <v-card-text>
                <v-alert
                  v-model="errorAlert"
                  dismissible
                  outline
                  transition="scale-transition"
                  type="error">
                  {{error_message}}
                </v-alert>
                <v-form ref="loginForm" v-model="valid" lazy-validation>
                  <v-text-field
                    type="username"
                    v-model="username"
                    :rules="usernameRules"
                    :class="{ 'v-input-group--dirty': dirtyLogin }"
                    label="Login"
                    prepend-icon="person"
                    required
                  ></v-text-field>
                  <v-text-field
                    v-model="password"
                    :rules="passwordRules"
                    :class="{ 'v-input-group--dirty': dirtyPwd }"
                    label="Password"
                    prepend-icon="lock"
                    type="password"
                    required
                    name="password"
                  ></v-text-field>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-btn
                  :disabled="!valid"
                  color="primary"
                  @click="submitForm"
                >Login</v-btn>
                <v-spacer></v-spacer>
                <v-btn
                  color="primary"
                  @click="$store.dispatch('APP_QUIT')"
                >Exit</v-btn>
              </v-card-actions>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </v-app>
</template>
<script>
  // import {AUTH_REQUEST} from '@/store/actions/auth'

  export default {
    name: 'login',
    data: function () {
      return {
        dirtyPwd: null,
        dirtyLogin: null,
        valid: false,
        username: '',
        errorAlert: false,
        message: 'Login to SpeakerGuru platform',
        error_message: '',
        usernameRules: [
          v => !!v || 'Login is required'
        ],
        password: '',
        passwordRules: [
          v => !!v || 'Password is required'
        ]
        /* loading: false,
        show_tab: false,
        show_bg_img: false,
        show_logo_img: false,
        show_form: false,
        show_pass: false,
        bg_img: 'static/img/login/login_bg.jpg',
        logo_img: 'static/img/login/mittech_logo.png',
        valid: false,
        username: null,
        password: null,
        rules: {
          required: function (value) {
            return value ? true : 'Это обязательное поле'
          }
        } */
      }
    },

    methods: {
      submitForm: function () {
        this.errorAlert = false
        if (!this.$refs.loginForm.validate()) {
          return false
        }
        const {username, password} = this
        const self = this
        this.$store.dispatch('AUTH_REQUEST', {username, password}).then(() => {
          self.$router.push('/')
        }).catch((err) => {
          console.log(err)
          self.errorAlert = true
          self.error_message = err
        })
          .finally((f) => {
            console.log('gotit')
          })
      }
    },

    created: function () {
    },
    mounted () {
      let times = 0
      const interval = setInterval(() => {
        times += 1
        if ((this.dirtyLogin && this.dirtyPwd) || times === 20) {
          clearInterval(interval)
        }
        this.dirtyLogin = document.querySelector('input[type="username"]:-webkit-autofill')
        this.dirtyPwd = document.querySelector('input[type="password"]:-webkit-autofill')
      }, 100)
    }
  }
</script>

<style scoped>



</style>



