import axios from 'axios'
import apiUrls from './api_urls';
import settings from '../settings/settings';
import store from '../store'

const apiCall = function ({url, data, params, url_ext}) {
    if (!url) {
        console.log('apiCall: params not send', url, data);
        return new Promise((resolve, reject) => {
            reject(new Error('params not send'))
        })
    }

    // URL CONFIG
    const conf = apiUrls[url];
    if (!conf) {
        console.log('apiCall: config not found', url, data);
        return new Promise((resolve, reject) => {
            reject(new Error('config not found'))
        })
    }
    const curr_url = url_ext ? conf.url + url_ext : conf.url;

    // HEADERS
    const headers = conf.headers ? conf.headers : settings.base_api_headers;
    if (store.getters.isAuthenticated) {
        headers["Authorization"] = 'Bearer ' + store.getters.accessToken;
    }

    // PARAMS
    params = Object.assign(conf.params || {}, params || {});
    //params = conf.params ? conf.params : {};

    // AXIOS CREATE
    let config = {
        baseURL: settings.base_api_url,
        timeout: 50000,
        withCredentials: false,
        headers: headers,
        params: params
    };

    // AXIOS PARAMS
    if (conf.axios) {
        Object.keys(conf.axios).forEach((key) => {
            config[key] = conf.axios[key]
        });
    }

    switch (conf.method) {
        case 'GET':
            return axios.get(curr_url, config);
        case 'DELETE':
            return axios.delete(curr_url, config);
        case 'POST':
            return axios.post(curr_url, data, config);
    }
};





export default apiCall



