<template>
  <v-app id="inspire">
    <v-content>
      <v-container fluid fill-height>
        <v-layout align-center justify-center>
          <v-flex xs12 sm8 md4>
            <v-card class="elevation-12">
              <v-card-text>
                <v-toolbar dark color="primary">

                  <v-toolbar-title>
                    {{message}}
                  </v-toolbar-title>

                  <v-icon
                    small
                    @click="$store.dispatch('USER_REQUEST','{sessionCode:props.item}')"
                  >
                    refresh
                  </v-icon>

                  <v-spacer></v-spacer>

                  <v-icon
                    small
                    @click="logout"
                  >
                    exit_to_app
                  </v-icon>
                </v-toolbar>
                <v-form ref="sessionsForm">
                  <template style="overflow: auto">
                    <v-data-table
                      :headers="headers"
                      :items="sessions"
                      class="elevation-1"
                      hide-actions
                    >
                      <template slot="items"  slot-scope="props">
                        <tr>
                          <td class="text-xs-left">{{ props.item.dt }} {{ props.item.tmstart }} {{ props.item.tmend }}</td>
                          <td class="text-xs-left">{{ props.item.subject }}</td>
                          <td class="text-xs-left">{{ props.item.count_listeners }}</td>
                          <td class="text-xs-left">
                            <v-btn
                              @click="$store.dispatch('APP_JOIN','{sessionCode:props.item}')"
                              color="primary"

                            >Join</v-btn>
                          </td>
                        </tr>
                      </template>
                    </v-data-table>
                  </template>
                </v-form>
              </v-card-text>

            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
  import { mapGetters } from 'vuex'

  export default {
    name: 'account',
    methods: {
      logout: function () {
        const self = this
        this.$store.dispatch('AUTH_LOGOUT')
          .then(() => {
            self.$router.push('/login')
          })
      }
    },

    data: function () {
      return {
        message: '',
        chosenSessCode: false,
        headers: [
          {
            text: 'TIME',
            align: 'left',
            sortable: false
          },
          {
            text: 'NAME',
            align: 'left',
            sortable: false
          },
          {
            text: 'LISTNERS',
            align: 'left',
            sortable: false
          }
        ]
      }
    },
    computed: {
      ...mapGetters(['sessions'])
    },
    mounted () {
      this.$store.dispatch('USER_REQUEST')
    }
  }
</script>

<style scoped>

</style>



